
.body{
    background-color: #000000;
    color: #ffffff;
    padding-top:50px; 
    /* padding-bottom:50px;  */
    width: 100vw;
    border-bottom: 2px #b0161f solid;
    font-family:"微软雅黑";
}
.body .circle_icon{
    margin-left: -50px;
    height: 400px;
    width: 400px;
}
.body .circle_right{
    margin: 100px 0;
    text-align: left;
}
.body .circle_right .fontA{
    font-size: 28px;
    margin: 10px 0;
    color: #10a1be;
    white-space: nowrap;
}
.body .circle_right .fontB{
    font-size: 28px;
    margin: 25px 0;
    white-space: nowrap;
}
.body .my_input{
    width: 24.8vw;
    height: 36px;
    background-color: #000000;
    color: #ffffff;
    border: 1px #ffffff solid;
    text-indent:10px;
    font-size: 18px;
}
.body .my_input2{
    width: 24.8vw;
    height: 140px;
    background-color: #000000;
    color: #ffffff;
    border: 1px #ffffff solid;
    text-indent:10px;
    font-size: 18px;
    text-align: top;
}
.body .ddzj_icon{
    height: 64px;
    width: 256px;
}
.body .contentP{
    text-align: center;
}
.body .body_line,
.body .body_line2{
    margin:0 auto;
    height: 1px;
    width: 100%;
    background: radial-gradient(#ffffff 24%, #000000 100%);
}
.body .titleInfo{
    text-align: center;
    font-size: 24px;
    white-space: nowrap;
}
.body .content{
    text-align: left;
}
.body .btnReg{
    background-color: #b0161f;
    width: 180px;
    height: 50px;
    display: inline-block;
    line-height: 0;
    white-space: nowrap;
    cursor: pointer;
    /* background: #fff; */
    border: 1px solid #b0161f;
    /* color: #1f2d3d; */
    color: #ffffff;
    /* margin: 0; */
    margin: 40px 0 60px 0; 
    padding: 10px 15px;
    /* border-radius: 4px; */
    font-size: 22px;
}
.body .btnReg2{
    width: 150px;
    background-color: #b0161f;
    color: #ffffff;
}

.body .el-input__inner{
    background-color: #000000;
    margin: 3px 0;
    color: #ffffff;
}
.body .sTitle{
    margin: 15px 0 5px 0;
    font-size: 22px;
}
.body .sign {
    color: #b0161f;
}
.body .ErrorInfo {
    color: #b0161f;
    font-size: 12px;
    margin: 3px 0;
}
.body .el-input {
    width: 24.8vw;
}
.body .el-select>.el-input {
    width: 24.8vw;
}

.body .el-textarea{
    background-color: #000000;
}
.body .el-textarea__inner {
    color: #ffffff;
    background-color: #000000;
    font-size: 16px;
}

.body .my_select{
    width: 24.8vw;
    color: #ffffff;
    background-color: #000000;
    text-indent:10px;
    height: 40px;
    border: 1px #ffffff solid;
    font-size: 18px;


    /* 清除默认边框 */
    /* border: 0; */
    /* 清除默认的箭头样式 */
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    /* 右侧添加小箭头的背景图 */
    background: url('../assets/veritas/select_icon.png') 97% center no-repeat;
    background-size: 18px;

}

.body .message_box{
    height: 1000vh;
    width: 100vw;
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 10;
    text-align: center;
    vertical-align: center;
    color:#000000;
}
.body .message_content{
    background-color: #ffffff;
    border-top: 6px solid brown;
}
.body .ok_icon{
    height: 76px;
    width: 76px;
    text-align: center;
    vertical-align: center;

}
.body .fontm1{
    color: brown;
    font-size: 20px;
    font-weight: bold;
}
.body .fontm2{
    font-size: 14px;
    color: brown;
}

.body .cancel{
    position: absolute;
   margin-left: 25vw;
    margin-top: 0vh;
    font-size: 40px;
    color: brown;
}

