        margin: 60vh auto 0 auto;
        
    }
    .body .ok_icon{
        margin: 3vw auto 0 auto;
    }
    .body .message_infoPhone{
        display: inherit;
    }
    .body .message_infoPC{
        display: none;
    }
    .body .cancel{
        margin-left: 12vw;
    }
    .body .circle_right .fontA{
        font-size: 4.9vw;
    }
    .body .circle_right .fontB{
        font-size: 4.9vw;
    }
    .body .titleInfo{
        font-size: 4.9vw;
    }
    .body .ddzj_icon{
        width: 32vw;
        height: 8vw;
    }

    .body .content{
        margin: 0 5px;
    }
    .body .sTitle{
        font-size: 4.9vw;
    }
    .body .my_select{
        font-size: 3.3vw;
    }
    .body .btnReg{
        font-size: 4.9vw;
    }

}

