



.header{
    text-align: left;
    height: 80px;
    line-height: 80px;
    font-family:"微软雅黑";
}

.header .left_logo{
    margin-top: 8px;
    margin-bottom: -8px;
}

.header .mypic{
    margin-top: 10px;
}

.header .home_icon,
.header .weixin_icon{
    height: 20px;
    line-height: 20px;
    width: 20px;
    vertical-align: middle;
    margin-top: -5px; 
}

.header .logo_icon{
    height: 30px;
}

.header .header_title{
    font-size: 16px;
    /* vertical-align: middle; */
    height: 24px;
    line-height: 24px;
    /* vertical-align: middle; */

}
.header .title_details{
    font-size: 16px;
    height: 24px;
    line-height: 24px;
    /* vertical-align: middle; */
    /* margin-top:30px;  */
}
.header .header_title:hover{
    /* text-decoration: underline; */
    /* color: red; */
    /* cursor: hand; */
}

.header .header_title .header_title_c:hover{
    text-decoration: underline;
    color: red;
    cursor: hand;
}

.header .header_title2{
    font-size: 16px;
    color: #b6363a;
    line-height: 20px;
}
.header .hamburger_icon{
    margin-top: 22px;
}

.header .menu{
    /* z-index: 9999999  !important; */
    background-color: #ffffff;
    text-align: center;
    border-top: 1px #b6363a solid;
    border-bottom: 1px #b6363a solid;
}
.header .menu p{
    /* z-index: 9999999  !important; */
    border-top: 1px #b6363a solid;
    border-bottom: 1px #b6363a solid;
    margin: 0 0;
    line-height: 60px;
    height: 60px;
    /* background-color: #bd8e8e; */
}



@media screen and (max-width: 1199px) {

    .header .sreenPC{
        display: none;
    }
    .header .sreenPhone{
        display: inherit;
    }
}

@media screen and (min-width: 1200px) {

    .header .sreenPC{
        display: inherit;

    }
    .header .sreenPhone{
        display: none;

    }
}

